* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-size: 20px;
}
body {
  margin: 0;
  padding: 60px;
}
.container {
  background-color:aliceblue;
  border: 1px solid rgb(20, 132, 230);
  border-radius: 15px;
  margin: auto;
  margin-top: 50px;
  padding: 20px;
  text-align: center;
}
.container-item {
  position: relative;
  text-align: center;
  padding: 20px;
}
.container-item:hover {
  background-color: rgba(131, 196, 252, 0.484);
  border-radius: 15px;
  color: rgb(255, 255, 255);
  font-size: 21px;
  transition: 150ms;
}
.container-item__img {
  border-radius: 15px;
  height: auto;
  display: inline-block;
  margin: 0 auto;
  min-width: 20%;
  width: auto;
  max-width: 100%;
}
.container-item__li{
  margin: 10px;
  padding: 25px;
}
.container-ol {
  padding: 0;
}
h1 {
  color: rgba(0, 0, 0, 0.756);
}
h2 {
  position: relative;
  background-color: aliceblue;
  color: rgb(65, 77, 242);
  font-size: 30px;
  font-family: 'Sans-Serif', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin: 20px;
  padding: 20px;
}
li:hover{
  background-color: rgba(131, 196, 252, 0.484);
  border-radius: 15px;
  color: rgb(255, 255, 255);
  font-size: 21px;
  transition: 150ms;
}
ol {
  padding: 0;
}
form {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}
input {
  border: 1px solid rgb(10, 129, 234);
  border-radius: 15px;
  width: 90%;
  padding: 5px 10px;
}
@media screen and (max-width:500px) {
  * {
    font-size: 10px;
  }
  .container {
    margin-top: 20px;
    padding: 10px;
  }
  .container-item {
    padding: 10px;
  }
  .container-item__img {
    /*min-width: 20%;*/
    max-width: 100%;
  }
  .container-item__li{
    margin: 5px;
    padding: 15px;
  }
  h1 {
    color: rgba(0, 0, 0, 0.756);
  }
  h2 {
    color: rgb(65, 77, 242);
    font-size: 20px;
    margin: 15px;
    padding: 15px;
  }
  /*li:hover{
    background-color: rgba(131, 196, 252, 0.484);
    border-radius: 15px;
    color: rgb(255, 255, 255);
    font-size: 21px;
    transition: 150ms;
  }*/
  input {
    width: 80%;
    padding: 5px
  }
  .root-name {
    color: blue;
  }
}
